<template>
  <div class="service-detail">
    <el-form
      class="service-detail__form"
      label-position="right"
      ref="serviceForm"
      :model="productForm"
      label-width="200px"
    >
      <el-collapse v-model="activeNames">
        <el-collapse-item name="basic">
          <template slot="title">
            <h2>基本信息</h2>
          </template>
          <div class="margin-right-30 margin-top-20 form-width">
            <el-form-item
              label="名称"
              prop="title"
              :rules="[
                {
                  required: true,
                  message: '请填写套餐名称主标题',
                  trigger: 'blur'
                }
              ]"
            >
              <span class="subtitle">主标题</span>
              <div class="form-item">
                <div class="title-10words">
                  <el-input
                    :disabled="pageType === 'update'"
                    :readonly="pageReadOnly"
                    v-model="productForm.title"
                    size="small"
                    maxlength="10"
                    show-word-limit
                    placeholder="请填写套餐名称主标题"
                  />
                </div>
                <div class="red-tips margin-left-20">
                  {{ serviceTips.title }}
                </div>
              </div>
            </el-form-item>
            <div style="height: 10px"></div>
            <el-form-item
              label
              prop="sub_title"
              :rules="[
                {
                  required: true,
                  message: '请填写套餐名称副标题',
                  trigger: 'blur'
                }
              ]"
            >
              <span class="subtitle">副标题</span>
              <div class="title-10words">
                <el-input
                  :readonly="pageReadOnly"
                  :disabled="pageType === 'update'"
                  v-model="productForm.sub_title"
                  size="small"
                  maxlength="10"
                  show-word-limit
                  placeholder="请填写套餐名称副标题"
                />
              </div>
            </el-form-item>
            <el-form-item
              label="套餐类型"
              prop="department"
              :rules="[
                { required: true, message: '请选择套餐类型', trigger: 'change' }
              ]"
            >
              <el-select
                :disabled="pageType === 'update' || pageReadOnly"
                v-model="productForm.department"
                placeholder="请选择套餐类型"
                size="small"
              >
                <el-option
                  v-for="item in packageType"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                />
              </el-select>
            </el-form-item>
            <el-form-item
              label="原价"
              prop="market_price"
              :rules="[
                { required: true, message: '请填写套餐原价', trigger: 'blur' }
              ]"
            >
              <div class="form-item">
                <div class="title-10words">
                  <el-input
                    @input="setNumber($event, 'mp')"
                    @change="toFixedNum($event, 'mp')"
                    :readonly="pageReadOnly"
                    v-model="productForm.market_price"
                    size="small"
                    placeholder="请填写套餐原价"
                  />
                </div>
                <div class="red-tips margin-left-20">
                  {{ serviceTips.marketPrice }}
                </div>
              </div>
            </el-form-item>
            <el-form-item
              label="现价"
              prop="price"
              :rules="[
                { required: true, message: '请填写套餐现价', trigger: 'blur' }
              ]"
            >
              <div class="form-item">
                <div class="title-10words">
                  <el-input
                    @input="setNumber($event, 'p')"
                    @change="toFixedNum($event, 'p')"
                    :readonly="pageReadOnly"
                    v-model="productForm.price"
                    size="small"
                    placeholder="请填写套餐现价"
                  />
                </div>
                <div class="red-tips margin-left-20">
                  {{ serviceTips.price }}
                </div>
              </div>
            </el-form-item>
            <el-form-item
              label="服务周期"
              prop="duration"
              :rules="[
                { required: true, message: '请选择服务周期', trigger: 'change' }
              ]"
            >
              <div class="form-item">
                <div>
                  <el-input-number
                    :disabled="pageType !== 'add'"
                    v-model="productForm.duration"
                    :step="1"
                    :min="1"
                    size="small"
                  ></el-input-number>
                  <span
                    >&nbsp;个月&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span
                  >
                </div>
                <div class="red-tips margin-left-20">
                  {{ serviceTips.duration }}
                </div>
              </div>
            </el-form-item>
            <el-form-item
              label="电子健康评估档案费"
              prop="base_price"
              :rules="[
                {
                  required: true,
                  message: '请填写电子健康评估档案费',
                  trigger: 'blur'
                }
              ]"
            >
              <div class="form-item">
                <div class="title-10words">
                  <el-input
                    @input="setNumber($event, 'bp')"
                    @change="toFixedNum($event, 'bp')"
                    :disabled="pageReadOnly"
                    v-model="productForm.base_price"
                    size="small"
                    placeholder="请填写电子健康评估档案费"
                  />
                </div>
                <div>
                  <span>&nbsp;元&nbsp;&nbsp;</span>
                  <span class="red-tips">{{ serviceTips.basePrice }}</span>
                </div>
              </div>
            </el-form-item>
          </div>
        </el-collapse-item>
        <el-collapse-item name="ask">
          <template slot="title">
            <h2>咨询配置</h2>
          </template>
          <div class="form-width">
            <el-form-item
              label="咨询方式"
              prop="ask_mode"
              :rules="[
                {
                  required: true,
                  message: '请选择咨询方式',
                  trigger: 'change'
                }
              ]"
            >
              <el-radio-group
                v-model="productForm.ask_mode"
                :disabled="pageReadOnly || pageType === 'update'"
              >
                <el-radio
                  :label="type.value"
                  v-for="(type, ind) in $store.state.dictionary.productTypes"
                  :key="type.value + ind"
                  >{{ type.label }}
                </el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item
              label="基础团队服务工作日"
              prop="serviceDays"
              :rules="[
                {
                  required: true,
                  message: '请选择基础团队服务工作日',
                  trigger: 'blur'
                }
              ]"
            >
              <el-checkbox
                :disabled="pageReadOnly"
                :indeterminate="isIndeterminate"
                v-model="checkAllDay"
                @change="handleCheckAllChange"
                >全选</el-checkbox
              >
              <div style="margin: 5px 0;"></div>
              <el-checkbox-group
                :disabled="pageReadOnly"
                v-model="productForm.serviceDays"
                @change="handleCheckedDayChange"
              >
                <el-checkbox
                  v-for="day in weekdays"
                  :label="day.value"
                  :key="day.value"
                  >{{ day.label }}</el-checkbox
                >
              </el-checkbox-group>
            </el-form-item>
            <el-form-item
              label="基础团队每日服务时间"
              prop="serviceTime"
              :rules="[
                {
                  required: true,
                  message: '请选择基础团队每日服务时间',
                  trigger: 'blur'
                }
              ]"
            >
              <div style="width: 350px">
                <el-time-picker
                  :readonly="pageReadOnly"
                  is-range
                  format="HH: mm"
                  size="small"
                  v-model="productForm.serviceTime"
                  range-separator="至"
                  start-placeholder="开始时间"
                  end-placeholder="结束时间"
                  placeholder="选择时间范围"
                ></el-time-picker>
              </div>
            </el-form-item>
            <HmForm
              v-if="productForm.department"
              :form-role="
                productForm.department === 'CHANKE' ? '产科医生' : '健康管理师'
              "
              :read-only="pageReadOnly"
              v-model="productForm.ask_hm"
              :form-type="productForm.ask_mode"
              ref="hmForm"
            ></HmForm>
            <DoctorAndDietitianForm
              v-if="productForm.department === 'ERKE'"
              :read-only="pageReadOnly"
              v-model="productForm.ask_doctor"
              :form-type="productForm.ask_mode"
              :form-role="'医生'"
              ref="doctorAndDietitianForm"
            ></DoctorAndDietitianForm>
            <DoctorAndDietitianForm
              v-if="productForm.department === 'ERKE'"
              :read-only="pageReadOnly"
              ref="dietitianForm"
              v-model="productForm.ask_dietitian"
              :form-type="productForm.ask_mode"
              :form-role="'营养师'"
            ></DoctorAndDietitianForm>
            <OtherDoctorForm
              v-if="productForm.department === 'CHANKE'"
              :read-only="pageReadOnly"
              v-model="productForm.ask_common"
              :form-type="productForm.ask_mode"
              ref="otherDoctorForm"
            >
            </OtherDoctorForm>
          </div>
        </el-collapse-item>
        <el-collapse-item name="service">
          <template slot="title">
            <h2>服务配置</h2>
          </template>
          <div class="form-width">
            <el-form-item
              label="请勾选需要开启的服务"
              prop="enabled_services"
              v-if="productForm.department"
            >
              <el-checkbox-group
                v-model="productForm.enabled_services"
                :disabled="pageReadOnly"
              >
                <el-checkbox
                  v-for="service in productForm.department === 'CHANKE'
                    ? chankeServices
                    : services"
                  :label="service.value"
                  :key="service.value"
                  >{{ service.label }}</el-checkbox
                >
              </el-checkbox-group>
            </el-form-item>
          </div>
        </el-collapse-item>
        <el-collapse-item name="group">
          <template slot="title">
            <h2>团队配置</h2>
          </template>
          <div class="form-width">
            <div class="corps-width">
              <BasicChankeSelect
                v-if="productForm.department === 'CHANKE'"
                :readonly="pageReadOnly"
                ref="basicGroupSelect"
                v-model="productForm.corps"
                :prop="'corps'"
                :label="'请选择基础服务团队'"
                :rules="[
                  {
                    required: true,
                    message: '请选择基础服务团队',
                    trigger: 'change'
                  }
                ]"
              ></BasicChankeSelect>
              <BasicGroupSelect
                v-if="productForm.department === 'ERKE'"
                :readonly="pageReadOnly"
                ref="basicGroupSelect"
                v-model="productForm.corps"
                :prop="'corps'"
                :label="'请选择基础服务团队'"
                :rules="[
                  {
                    required: true,
                    message: '请选择基础服务团队',
                    trigger: 'change'
                  }
                ]"
              ></BasicGroupSelect>
            </div>
            <el-form-item
              v-if="productForm.department"
              :label="
                productForm.department === 'ERKE'
                  ? '占用健康管理师服务资源量'
                  : '占用产科医生服务资源量'
              "
              prop="hmResource"
              :rules="[
                {
                  required: true,
                  message: '占用健康管理师服务/产科医生资源量',
                  trigger: 'change'
                }
              ]"
            >
              <div class="form-item">
                <div>
                  <el-input-number
                    :precision="2"
                    size="small"
                    :disabled="pageReadOnly"
                    v-model="productForm.hmResource"
                    :step="0.1"
                    :min="0"
                  ></el-input-number>
                </div>
                <div class="red-tips margin-left-20">
                  {{ serviceTips.hmResource }}
                </div>
              </div>
            </el-form-item>
            <el-form-item
              v-if="productForm.department === 'ERKE'"
              label="占用医生服务资源量"
              prop="doctorResource"
              :rules="[
                {
                  required: true,
                  message: '占用医生服务资源量',
                  trigger: 'change'
                }
              ]"
            >
              <div class="form-item">
                <div>
                  <el-input-number
                    size="small"
                    :disabled="pageReadOnly"
                    v-model="productForm.doctorResource"
                    :precision="2"
                    :step="0.1"
                    :min="0"
                  ></el-input-number>
                </div>
                <div class="red-tips margin-left-20">
                  {{ serviceTips.doctorResource }}
                </div>
              </div>
            </el-form-item>
            <div class="corps-width" v-if="productForm.department === 'ERKE'">
              <DietitianGroupSelect
                ref="dietitianGroupSelect"
                :readonly="pageReadOnly"
                v-if="dietitianTeamItemShow"
                v-model="productForm.dietitian_corps"
                :prop="'dietitian_corps'"
                :label="'请选择营养师团队'"
                :rules="[
                  {
                    required: true,
                    message: '请选择营养师团队',
                    trigger: 'change'
                  }
                ]"
              ></DietitianGroupSelect>
            </div>
            <el-form-item
              v-if="dietitianTeamItemShow && productForm.department === 'ERKE'"
              label="占用营养师服务资源量"
              prop="dietitianResource"
              :rules="[
                {
                  required: true,
                  message: '占用营养师服务资源量',
                  trigger: 'change'
                }
              ]"
            >
              <div class="form-item">
                <div>
                  <el-input-number
                    size="small"
                    :disabled="pageReadOnly"
                    v-model="productForm.dietitianResource"
                    :step="0.1"
                    :precision="2"
                    :min="0"
                  ></el-input-number>
                </div>
                <div class="red-tips margin-left-20">
                  {{ serviceTips.dietitianResource }}
                </div>
              </div>
            </el-form-item>
          </div>
        </el-collapse-item>
        <el-collapse-item name="other">
          <template slot="title">
            <h2>其他配置</h2>
          </template>
          <div class="form-width">
            <el-form-item
              label="套餐描述"
              prop="description"
              :rules="[
                {
                  required: true,
                  message: '请填写套餐描述',
                  trigger: 'blur'
                }
              ]"
            >
              <div style="width: 500px">
                <el-input
                  :readonly="pageReadOnly"
                  type="textarea"
                  v-model="productForm.description"
                  size="small"
                  placeholder="请填写套餐描述"
                />
              </div>
            </el-form-item>
            <el-form-item
              label="特色和亮点"
              prop="advantage"
              :rules="[
                {
                  required: true,
                  message: '请填写特色和亮点',
                  trigger: 'blur'
                }
              ]"
            >
              <div style="width: 500px">
                <el-input
                  :readonly="pageReadOnly"
                  type="textarea"
                  v-model="productForm.advantage"
                  size="small"
                  placeholder="请填写特色和亮点"
                />
              </div>
            </el-form-item>
          </div>
        </el-collapse-item>
      </el-collapse>
      <el-form-item v-if="this.pageAction !== '详情'">
        <div class="margin-top-20">
          <el-button type="primary" @click="submitForm('serviceForm')"
            >确认{{ this.pageAction }}</el-button
          >
        </div>
      </el-form-item>
    </el-form>
    <el-dialog title="套餐信息确认" :visible.sync="dialogVisible" width="650px">
      <div class="confirm">
        <div class="tips margin-left-20 margin-bottom-30">
          由于套餐配置项较多，此处仅展示关键项，请您在配置页仔细核对，
          一旦您修改了套餐内容，将立即更新至用户端小程序！
        </div>
        <div class="confirm__item">
          <div class="confirm__item--label">套餐名称：</div>
          <div class="confirm__item--content">
            {{ productForm.title + ' ' + productForm.sub_title }}
          </div>
        </div>
        <div class="confirm__item">
          <div class="confirm__item--label">套餐类型：</div>
          <div class="confirm__item--content">
            {{ productForm.department === 'ERKE' ? '儿科' : '产科' }}
          </div>
        </div>
        <div class="confirm__item">
          <div class="confirm__item--label">服务周期：</div>
          <div class="confirm__item--content">
            {{ productForm.duration }}个月
          </div>
        </div>
        <div class="confirm__item">
          <div class="confirm__item--label">原价：</div>
          <div class="confirm__item--content">
            {{ productForm.market_price }}.00 元
          </div>
        </div>
        <div class="confirm__item">
          <div class="confirm__item--label">现价：</div>
          <div class="confirm__item--content">
            {{ productForm.price }}.00 元
          </div>
        </div>
        <div class="confirm__item">
          <div class="confirm__item--label">固定建档费用：</div>
          <div class="confirm__item--content">
            {{ productForm.base_price }}.00 元
          </div>
        </div>
        <div class="confirm__item">
          <div class="confirm__item--label">咨询方式：</div>
          <div class="confirm__item--content">
            {{ productForm.ask_mode | productType }}
          </div>
        </div>
        <div class="confirm__item">
          <div class="confirm__item--label">咨询配置：</div>
          <div class="confirm__item--content">{{ consultList }}</div>
        </div>
        <div class="confirm__item">
          <div class="confirm__item--label">基础服务团队：</div>
          <div class="confirm__item--content">{{ commonGroupList() }}</div>
        </div>
        <div class="confirm__item" v-if="dietitianGroupList()">
          <div class="confirm__item--label">营养师团队：</div>
          <div class="confirm__item--content">{{ dietitianGroupList() }}</div>
        </div>
        <div class="confirm__item" v-if="servicesNameList()">
          <div class="confirm__item--label">服务配置：</div>
          <div class="confirm__item--content">{{ servicesNameList() }}</div>
        </div>
        <div class="confirm__item" v-if="pageAction === '修改'">
          <div class="confirm__item--label">
            <span style="color: red">*</span>修改原因：
          </div>
          <div class="confirm__item--content">
            <el-input
              type="textarea"
              :rows="2"
              placeholder="请输入内容"
              v-model="editReason"
            ></el-input>
          </div>
        </div>
        <div class="tips margin-left-20">
          请确保已向用户公示修改内容，避免引发用户投诉!
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <div style="text-align: center">
          <el-button @click="dialogVisible = false">返回修改</el-button>
          <el-button type="primary" @click="confirmForm" :loading="confirm"
            >确认{{ this.pageAction }}</el-button
          >
        </div>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import {
  addProducts,
  getServices,
  update,
  getEditInfo
} from '@/api/FdServiceType'
import _ from 'lodash'
import { getWeekOptions } from '@/utils/weekDays'
import { getDepartment } from '@/utils/dictionary'

import BasicGroupSelect from '@/components/Select/BasicGroupSelect'
import BasicChankeSelect from '@/components/Select/BasicChankeSelect.vue'
import DietitianGroupSelect from '@/components/Select/DietitianGroupSelect'
import HmForm from './components/HmForm'
import DoctorAndDietitianForm from './components/DoctorAndDietitianForm'
import OtherDoctorForm from './components/OtherDoctorForm'
import { serviceTips } from '@/utils/newService/serviceTips'
import { getParams, setParams } from '@/utils/newService/createParams'
const activeNames = ['basic', 'ask', 'service', 'other', 'group']
export default {
  components: {
    BasicGroupSelect,
    BasicChankeSelect,
    DietitianGroupSelect,
    HmForm,
    DoctorAndDietitianForm,
    OtherDoctorForm
  },
  data() {
    return {
      packageType: getDepartment('', 'arr'),
      confirm: false,
      serviceTips,
      activeNames: activeNames,
      productForm: {
        title: '',
        sub_title: '',
        department: '',
        corps: [],
        dietitian_corps: [],
        ask_mode: 'ROUND',
        description: '',
        advantage: '',
        enabled_services: [],
        market_price: null,
        price: null,
        base_price: null,
        duration: 1,
        serviceDays: [],
        serviceTime: '',
        hmResource: 0,
        dietitianResource: 0,
        doctorResource: 0,
        ask_hm: {
          counts: 0,
          counts_per_month: 0,
          round_counts: 0,
          appendable: true,
          first_delay_hour: 0,
          first_delay_minute: 0,
          common_delay_hour: 0,
          common_delay_minute: 0,
          max_duration_hour: 0,
          max_duration_minute: 0
        },
        ask_doctor: {
          enable: true,
          appendable: true,
          counts: 0,
          counts_per_month: 0,
          round_counts: 0,
          first_delay_hour: 0,
          first_delay_minute: 0,
          common_delay_hour: 0,
          common_delay_minute: 0,
          max_duration_hour: 0,
          max_duration_minute: 0,
          enable_voice: true,
          voice_counts: 0,
          voice_counts_per_month: 0
        },
        ask_dietitian: {
          enable: true,
          counts: 0,
          counts_per_month: 0,
          round_counts: 0,
          first_delay_hour: 0,
          first_delay_minute: 0,
          common_delay_hour: 0,
          common_delay_minute: 0,
          max_duration_hour: 0,
          max_duration_minute: 0,
          service_days: [],
          serviceTime: ''
        },
        ask_common: {
          enable: true,
          counts: 0,
          counts_per_month: 0,
          round_counts: 0,
          first_delay_hour: 0,
          first_delay_minute: 0,
          common_delay_hour: 0,
          common_delay_minute: 0,
          max_duration_hour: 0,
          max_duration_minute: 0,
          enable_voice: true,
          voice_counts: 0,
          voice_counts_per_month: 0,
          service_days: [],
          serviceTime: ''
        }
      },
      dialogVisible: false,
      editReason: '',
      weekdays: getWeekOptions(),
      isIndeterminate: false,
      checkAllDay: false,
      services: [],
      chankeServices: [],
      corps: []
    }
  },
  watch: {},
  created() {
    this.getServices()
    const productId = this.$route.params.id
    if (productId) {
      getEditInfo(productId).then(res => {
        const resFormData = _.cloneDeep(res.data)
        this.productForm = setParams(resFormData)
        this.checkAllDay = this.productForm.serviceDays.length === 7
        this.$nextTick(() => {
          if (this.$refs.dietitianForm) {
            this.$refs.dietitianForm.checkAllDay =
              this.productForm.ask_dietitian.service_days.length === 7
          }
          if (this.$refs.otherDoctorForm) {
            this.$refs.otherDoctorForm.checkAllDay =
              this.productForm.ask_common.service_days.length === 7
          }
        })
      })
    }
  },
  methods: {
    getDepartment,
    setNumber(value, type) {
      if (type === 'mp') {
        if (isNaN(Number(value))) this.productForm.market_price = ''
      }
      if (type === 'p') {
        if (isNaN(Number(value))) this.productForm.price = ''
      }
      if (type === 'bp') {
        if (isNaN(Number(value))) this.productForm.base_price = ''
      }
    },
    // 三种费用的表单验证
    toFixedNum(value, type) {
      if (!value) return
      if (type === 'mp') {
        if (Number(value)) {
          this.productForm.market_price = Number(value).toFixed(2)
        }
      }
      if (type === 'p') {
        if (Number(value)) {
          this.productForm.price = Number(value).toFixed(2)
        }
      }
      if (type === 'bp') {
        if (Number(value)) {
          this.productForm.base_price = Number(value).toFixed(2)
        }
      }
      if (
        this.productForm.market_price &&
        this.productForm.price &&
        Number(this.productForm.market_price) < Number(this.productForm.price)
      ) {
        this.$message.warning('原价必须小于现价')
        this.productForm.price = ''
      }
      if (
        this.productForm.base_price &&
        this.productForm.price &&
        Number(this.productForm.price) < Number(this.productForm.base_price)
      ) {
        this.$message.warning('固定建档费用必须小于现价')
        this.productForm.base_price = ''
      }
    },
    getSelectNameList(options, checkedList) {
      const checkedObjList = options.filter(item => {
        if (checkedList.includes(item.value)) {
          return item
        }
      })
      return checkedObjList.map(item => item.label).join(',')
    },
    servicesNameList() {
      return this.getSelectNameList(
        this.services,
        this.productForm.enabled_services
      )
    },
    dietitianGroupList() {
      if (this.$refs.dietitianGroupSelect) {
        return this.getSelectNameList(
          this.$refs.dietitianGroupSelect.options,
          this.productForm.dietitian_corps
        )
      }
      return ''
    },
    commonGroupList() {
      if (this.$refs.basicGroupSelect) {
        return this.getSelectNameList(
          this.$refs.basicGroupSelect.options,
          this.productForm.corps
        )
      }
      return ''
    },
    getServices() {
      getServices().then(res => {
        const services = res.data.services.map(service => {
          return {
            value: service.key,
            label: service.name
          }
        })
        this.services = services.filter(
          service =>
            service.value !== 'health_follow' &&
            service.value !== 'common_text_transfer' &&
            service.value !== 'common_voice_transfer'
        )
        this.chankeServices = services.filter(
          service =>
            service.value === 'health_follow' ||
            service.value === 'health_record' ||
            service.value === 'fd_ask' ||
            service.value === 'common_text_transfer' ||
            service.value === 'common_voice_transfer'
        )
      })
    },
    handleCheckedDayChange(value) {
      let checkedCount = value.length
      this.checkAllDay = checkedCount === this.weekdays.length
      this.isIndeterminate =
        checkedCount > 0 && checkedCount < this.weekdays.length
    },
    handleCheckAllChange(val) {
      this.productForm.serviceDays = val
        ? getWeekOptions().map(item => item.value)
        : []
      this.isIndeterminate = false
    },
    submitForm(formName) {
      const mainFormValid = this.$refs[formName].validate()
      let dietitianOrCommonFormValid
      if (this.productForm.department === 'ERKE') {
        dietitianOrCommonFormValid = this.$refs.dietitianForm.validForm()
      } else if (this.productForm.department === 'CHANKE') {
        dietitianOrCommonFormValid = this.$refs.otherDoctorForm.validForm()
      }
      Promise.all([mainFormValid, dietitianOrCommonFormValid])
        .then(res => {
          this.dialogVisible = true
        })
        .catch(error => {
          if (error.hm) {
            this.activeNames = ['ask']
            this.$message.error(error.hm)
          } else {
            this.activeNames = activeNames
          }
        })
    },
    confirmForm() {
      const params = getParams(this.productForm)
      this.confirm = true
      if (this.pageType === 'add') {
        addProducts(params)
          .then(res => {
            this.confirm = false
            this.$message.success('新增套餐成功')
            this.dialogVisible = false
            this.$router.back(-1)
          })
          .catch(error => {
            this.confirm = false
          })
      } else if (this.pageType === 'update') {
        params.update_remark = this.editReason
        update(this.$route.params.id, params)
          .then(res => {
            this.confirm = false
            this.$message.success('更新套餐成功')
            this.dialogVisible = false
            this.$router.back(-1)
          })
          .catch(error => {
            this.confirm = false
          })
      }
    }
  },
  computed: {
    consultList() {
      let doctorTextConsult = '',
        doctorVoiceConsult = '',
        dietitianTextConsult = ''
      if (this.productForm.department === 'ERKE') {
        doctorTextConsult = this.productForm.ask_doctor.enable
          ? '图文转诊医生'
          : ''
        doctorVoiceConsult = this.productForm.ask_doctor.enable_voice
          ? '电话转诊医生'
          : ''
        dietitianTextConsult = this.productForm.ask_dietitian.enable
          ? '图文转诊营养师'
          : ''
      } else if (this.productForm.department === 'CHANKE') {
        doctorTextConsult = this.productForm.ask_common.enable
          ? '图文转诊其他科室'
          : ''
        doctorVoiceConsult = this.productForm.ask_common.enable_voice
          ? '电话转诊其他科室'
          : ''
      }
      return `健康咨询,${doctorTextConsult},${doctorVoiceConsult},${dietitianTextConsult}`
    },
    pageReadOnly() {
      return this.pageType === 'detail'
    },
    dietitianTeamItemShow() {
      return (
        this.productForm.ask_dietitian.enable ||
        this.enabledServicesIncludeRecipes
      )
    },
    enabledServicesIncludeRecipes() {
      return this.productForm.enabled_services.includes('recipe_service')
    },
    pageType() {
      if (this.$route.params.type === 'update') {
        return 'update'
      } else if (this.$route.params.type === 'detail') {
        return 'detail'
      } else {
        return 'add'
      }
    },
    pageAction() {
      switch (this.pageType) {
        case 'add':
          return '新增'
        case 'update':
          return '修改'
        case 'detail':
          return '详情'
        default:
          return ''
      }
    }
  }
}
</script>

