export function addGroup(params) {
  return request({
    url: '/fd/corps',
    method: 'post',
    data: params
  })
}
export function changeOnlineStatus(id, params) {
  return request({
    url: `/fd/corps/${id}/status`,
    method: 'put',
    data: params
  })
}

export function putGroup(id, params) {
  return request({
    url: `/fd/corps/${id}`,
    method: 'put',
    data: params
  })
}

export function getEditInfo(id) {
  return request({
    url: `/fd/corps/${id}`,
    method: 'get'
  })
}
