<template>
  <div>
    <h3 class="margin-bottom-30">其他科室服务</h3>
    <div class="other-tips">
      注：目前其他科室服务团队包含内分泌科医生和营养师，共享转诊次数，营养师暂无电话转诊功能。
    </div>
    <el-form
      :model="value"
      label-position="right"
      ref="doctorAndDietitianForm"
      label-width="220px"
    >
      <el-form-item
        label="是否允许图文转诊"
        prop="enable"
        :rules="[
          {
            required: true,
            message: '',
            trigger: 'change'
          }
        ]"
      >
        <el-radio-group v-model="value.enable" :disabled="readOnly">
          <el-radio :label="true">是</el-radio>
          <el-radio :label="false">否</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item
        label="服务期内图文转诊总次数"
        v-if="value.enable"
        prop="counts"
        :rules="[
          {
            required: true,
            message: '请选择服务期内咨询总次数',
            trigger: 'change'
          }
        ]"
      >
        <div class="form-item">
          <div>
            <el-input-number
              :disabled="readOnly"
              size="small"
              v-model="value.counts"
              :step="1"
              :min="1"
            ></el-input-number>
          </div>
          <div class="red-tips margin-left-20">
            {{ serviceTips.otherCounts }}
          </div>
        </div>
      </el-form-item>
      <el-form-item
        v-if="value.enable"
        label="每月图文转诊次数上限"
        prop="counts_per_month"
        :rules="[
          {
            required: true,
            message: '每月图文转诊次数上限',
            trigger: 'change'
          }
        ]"
      >
        <div class="form-item">
          <div>
            <el-input-number
              :disabled="readOnly"
              size="small"
              v-model="value.counts_per_month"
              :step="1"
              :min="1"
              :max="value.counts"
            ></el-input-number>
          </div>
          <div class="red-tips margin-left-20">
            {{ serviceTips.otherCountsPerMonth }}
          </div>
        </div>
      </el-form-item>
      <el-form-item
        label="图文转诊回合数上限"
        v-if="formType === 'ROUND' && value.enable"
        prop="round_counts"
        :rules="[
          {
            required: true,
            message: '请选择单次咨询回合数上限',
            trigger: 'change'
          }
        ]"
      >
        <div class="form-item">
          <div>
            <el-input-number
              size="small"
              :disabled="readOnly"
              v-model="value.round_counts"
              :step="1"
              :min="1"
            ></el-input-number>
          </div>
          <div class="red-tips margin-left-20">
            {{ serviceTips.askHmRoundCounts }}
          </div>
        </div>
      </el-form-item>
      <el-form-item
        v-if="value.enable"
        :label="
          formType === 'ROUND'
            ? '图文转诊首回合响应时长'
            : '转诊成功后首次响应时间上限'
        "
        prop="first_delay_hour"
        :rules="[
          {
            required: true,
            message: '',
            trigger: 'change'
          }
        ]"
      >
        <div class="form-item">
          <div>
            <el-input-number
              @change="onHourChange($event, 'first')"
              :disabled="readOnly"
              size="small"
              v-model="value.first_delay_hour"
              :step="1"
              :min="0"
            ></el-input-number>
            <span>&nbsp;小时&nbsp;&nbsp;&nbsp;&nbsp;</span>
            <el-input-number
              :disabled="readOnly"
              size="small"
              v-model="value.first_delay_minute"
              :step="10"
              :max="59"
              :min="getDelayMinuteFrom().first"
            ></el-input-number>
            <span>&nbsp;分钟</span>
          </div>
          <div class="red-tips margin-left-20">
            {{ serviceTips.otherFirstDelay }}
          </div>
        </div>
      </el-form-item>
      <el-form-item
        :disabled="readOnly"
        v-if="value.enable"
        :label="
          formType === 'ROUND'
            ? '图文转诊非首回合响应时长'
            : '转诊成功后非首次响应时间上限'
        "
        prop="common_delay_hour"
        :rules="[
          {
            required: true,
            message: '请选择咨询首次响应时间上限',
            trigger: 'change'
          }
        ]"
      >
        <div class="form-item">
          <div>
            <el-input-number
              @change="onHourChange($event, 'common')"
              size="small"
              :disabled="readOnly"
              v-model="value.common_delay_hour"
              :step="1"
              :min="0"
            ></el-input-number>
            <span>&nbsp;小时&nbsp;&nbsp;&nbsp;&nbsp;</span>
            <el-input-number
              size="small"
              :disabled="readOnly"
              v-model="value.common_delay_minute"
              :step="10"
              :max="59"
              :min="getDelayMinuteFrom().common"
            ></el-input-number>
            <span>&nbsp;分钟</span>
          </div>
          <div class="red-tips margin-left-20">
            {{ serviceTips.otherCommonDelay }}
          </div>
        </div>
      </el-form-item>
      <el-form-item
        v-if="value.enable"
        :label="
          formType === 'ROUND'
            ? '图文转诊每回合用户响应时长'
            : '图文转诊用户响应时长'
        "
        prop="max_duration_hour"
        :rules="[
          {
            required: true,
            message: '图文转诊每回合用户响应时长',
            trigger: 'change'
          }
        ]"
      >
        <div class="form-item">
          <div>
            <el-input-number
              @change="onHourChange($event, 'duration')"
              size="small"
              :disabled="readOnly"
              v-model="value.max_duration_hour"
              :step="1"
              :min="0"
            ></el-input-number>
            <span>&nbsp;小时&nbsp;&nbsp;&nbsp;&nbsp;</span>
            <el-input-number
              size="small"
              :disabled="readOnly"
              v-model="value.max_duration_minute"
              :step="10"
              :max="59"
              :min="getDelayMinuteFrom().duration"
            ></el-input-number>
            <span>&nbsp;分钟</span>
          </div>
          <div class="red-tips margin-left-20">
            {{ serviceTips.doctorMaxDuration }}
          </div>
        </div>
      </el-form-item>
      <el-form-item
        label="是否允许电话转诊"
        prop="enable_voice"
        :rules="[
          {
            required: true,
            message: '',
            trigger: 'change'
          }
        ]"
      >
        <el-radio-group v-model="value.enable_voice" :disabled="readOnly">
          <el-radio :label="true">是</el-radio>
          <el-radio :label="false">否</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item
        v-if="value.enable_voice"
        label="服务期内电话转诊总次数"
        prop="counts"
        :rules="[
          {
            required: true,
            message: '请选择服务期内电话转诊总次数',
            trigger: 'change'
          }
        ]"
      >
        <div class="form-item">
          <div>
            <el-input-number
              size="small"
              :disabled="readOnly"
              v-model="value.voice_counts"
              :step="1"
              :min="1"
            ></el-input-number>
          </div>
          <div class="red-tips margin-left-20">
            {{ serviceTips.otherVoiceCounts }}
          </div>
        </div>
      </el-form-item>
      <el-form-item
        v-if="value.enable_voice"
        label="每月电话转诊次数上限"
        prop="counts"
        :rules="[
          {
            required: true,
            message: '每月电话转诊次数',
            trigger: 'change'
          }
        ]"
      >
        <div class="form-item">
          <div>
            <el-input-number
              size="small"
              :disabled="readOnly"
              v-model="value.voice_counts_per_month"
              :step="1"
              :min="1"
              :max="value.voice_counts"
            ></el-input-number>
          </div>
          <div class="red-tips margin-left-20">
            {{ serviceTips.otherVoiceCountsPerMonth }}
          </div>
        </div>
      </el-form-item>
      <el-form-item
        label="其他科室人员服务工作日"
        v-if="value.enable"
        prop="service_days"
        :rules="[
          {
            required: true,
            message: '其他科室人员服务工作日',
            trigger: 'change'
          }
        ]"
      >
        <el-checkbox
          :disabled="readOnly"
          :indeterminate="isIndeterminate"
          v-model="checkAllDay"
          @change="handleCheckAllChange"
          >全选</el-checkbox
        >
        <div style="margin: 5px 0;"></div>
        <el-checkbox-group
          :disabled="readOnly"
          v-model="value.service_days"
          @change="handleCheckedDayChange"
        >
          <el-checkbox
            v-for="day in weekdays"
            :label="day.value"
            :key="day.value"
            >{{ day.label }}</el-checkbox
          >
        </el-checkbox-group>
      </el-form-item>
      <el-form-item
        v-if="value.enable"
        label="其他科室医生每日服务时间"
        prop="serviceTime"
        :rules="[
          {
            required: true,
            message: '其他科室医生每日服务时间',
            trigger: 'blur'
          }
        ]"
      >
        <div style="width: 350px">
          <el-time-picker
            :readonly="readOnly"
            is-range
            format="HH : mm"
            size="small"
            v-model="value.serviceTime"
            range-separator="至"
            start-placeholder="开始时间"
            end-placeholder="结束时间"
            placeholder="选择时间范围"
          >
          </el-time-picker>
        </div>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { getWeekOptions } from '@/utils/weekDays'
import { serviceTips } from '@/utils/newService/serviceTips'

export default {
  props: {
    value: {
      type: Object,
      default: () => {}
    },
    formType: {
      type: String,
      default: ''
    },
    readOnly: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      serviceTips,
      weekdays: getWeekOptions(),
      isIndeterminate: false,
      checkAllDay: false
    }
  },
  methods: {
    onHourChange(val, type) {
      if (type === 'first') {
        if (val === 0) {
          this.value.first_delay_minute = 10
        }
      } else if (type === 'common') {
        if (val === 0) {
          this.value.common_delay_minute = 10
        }
      } else if (type === 'duration') {
        if (val === 0) {
          this.value.max_duration_minute = 10
        }
      }
    },
    getDelayMinuteFrom() {
      let firstMinuteFrom = 15
      let commonMinuteFrom = 15
      let maxDurationMinuteFrom = 15
      if (this.value.first_delay_hour > 0) {
        firstMinuteFrom = 0
      }
      if (this.value.common_delay_hour > 0) {
        commonMinuteFrom = 0
      }
      if (this.value.max_duration_hour > 0) {
        maxDurationMinuteFrom = 0
      }
      return {
        first: firstMinuteFrom,
        common: commonMinuteFrom,
        duration: maxDurationMinuteFrom
      }
    },
    handleCheckedDayChange(value) {
      let checkedCount = value.length
      this.checkAllDay = checkedCount === this.weekdays.length
      this.isIndeterminate =
        checkedCount > 0 && checkedCount < this.weekdays.length
    },
    handleCheckAllChange(val) {
      this.value.service_days = val
        ? getWeekOptions().map(item => item.value)
        : []
      this.isIndeterminate = false
    },
    validForm() {
      return this.$refs.doctorAndDietitianForm.validate()
    }
  }
}
</script>

<style scoped lang="scss">
.form-item {
  display: flex;
}
.red-tips {
  font-size: 12px;
  color: #f8747f;
}
.other-tips {
  margin-top: -10px;
  margin-left: 20px;
  font-size: 12px;
  color: #f8747f;
}
</style>
