<template>
  <div>
    <h3 class="margin-bottom-30">{{ formRole }}服务</h3>
    <el-form
      :model="value"
      label-position="right"
      ref="hmForm"
      label-width="220px"
    >
      <el-form-item
        v-if="formType === 'ROUND'"
        label="服务期内咨询总次数"
        prop="counts"
        :rules="[
          {
            required: true,
            message: '请选择服务期内咨询总次数',
            trigger: 'change'
          }
        ]"
      >
        <div class="form-item">
          <div>
            <el-input-number
              size="small"
              :disabled="readOnly"
              v-model="value.counts"
              :step="1"
              :min="1"
            ></el-input-number>
          </div>
          <div class="red-tips margin-left-20">
            {{ serviceTips.askHmCounts }}
          </div>
        </div>
      </el-form-item>
      <el-form-item
        v-if="formType === 'ROUND'"
        label="每月咨询次数上限"
        prop="counts_per_month"
        :rules="[
          {
            required: true,
            message: '请选择每月咨询次数上限',
            trigger: 'change'
          }
        ]"
      >
        <div class="form-item">
          <div>
            <el-input-number
              size="small"
              :disabled="readOnly"
              v-model="value.counts_per_month"
              :step="1"
              :min="1"
              :max="value.counts"
            ></el-input-number>
          </div>
          <div class="red-tips margin-left-20">
            {{ serviceTips.askHmCountsPerMonth }}
          </div>
        </div>
      </el-form-item>
      <el-form-item
        v-if="formType === 'ROUND'"
        label="单次咨询回合数上限"
        prop="round_counts"
        :rules="[
          {
            required: true,
            message: '请选择单次咨询回合数上限',
            trigger: 'change'
          }
        ]"
      >
        <div class="form-item">
          <div>
            <el-input-number
              size="small"
              :disabled="readOnly"
              v-model="value.round_counts"
              :step="1"
              :min="1"
            ></el-input-number>
          </div>
          <div class="red-tips margin-left-20">
            {{ serviceTips.askHmRoundCounts }}
          </div>
        </div>
      </el-form-item>
      <el-form-item
        v-if="formType === 'ROUND'"
        label="允许临时增加单次咨询回合数"
        prop="appendable"
        :rules="[
          {
            required: true,
            message: '允许临时增加单次咨询回合数',
            trigger: 'change'
          }
        ]"
      >
        <div class="form-item">
          <div>
            <el-radio-group v-model="value.appendable" :disabled="readOnly">
              <el-radio :label="true">是</el-radio>
              <el-radio :label="false">否</el-radio>
            </el-radio-group>
          </div>
          <div class="red-tips margin-left-20">
            {{ serviceTips.askHmAppendable }}
          </div>
        </div>
      </el-form-item>
      <el-form-item
        :label="
          formType === 'IM' ? '咨询首次响应时间上限' : '咨询首回合响应时长'
        "
        prop="first_delay_hour"
        :rules="[
          {
            required: true,
            message: '请选择咨询首次响应时间上限',
            trigger: 'change'
          }
        ]"
      >
        <div class="form-item">
          <div>
            <el-input-number
              @change="onHourChange($event, 'first')"
              size="small"
              :disabled="readOnly"
              v-model="value.first_delay_hour"
              :step="1"
              :min="0"
            ></el-input-number>
            <span>&nbsp;小时&nbsp;&nbsp;&nbsp;&nbsp;</span>
            <el-input-number
              size="small"
              :disabled="readOnly"
              v-model="value.first_delay_minute"
              :step="10"
              :min="getDelayMinuteFrom().first"
              :max="59"
            ></el-input-number>
            <span>&nbsp;分钟</span>
          </div>
          <div class="red-tips margin-left-20">
            {{
              formType === 'IM'
                ? serviceTips.askHmFirstDelay
                : serviceTips.askHmFirstDelayRound
            }}
          </div>
        </div>
      </el-form-item>
      <el-form-item
        :label="
          formType === 'IM' ? '咨询非首次响应时间上限' : '咨询非首回合响应时长'
        "
        prop="common_delay_hour"
        :rules="[
          {
            required: true,
            message: '请选择咨询首次响应时间上限',
            trigger: 'change'
          }
        ]"
      >
        <div class="form-item">
          <div>
            <el-input-number
              @change="onHourChange($event, 'common')"
              size="small"
              :disabled="readOnly"
              v-model="value.common_delay_hour"
              :step="1"
              :min="0"
            ></el-input-number>
            <span>&nbsp;小时&nbsp;&nbsp;&nbsp;&nbsp;</span>
            <el-input-number
              size="small"
              v-model="value.common_delay_minute"
              :step="10"
              :disabled="readOnly"
              :min="getDelayMinuteFrom().common"
              :max="59"
            ></el-input-number>
            <span>&nbsp;分钟</span>
          </div>
          <div class="red-tips margin-left-20">
            {{ serviceTips.askHmCommonDelay }}
          </div>
        </div>
      </el-form-item>
      <el-form-item
        :label="
          formType === 'ROUND'
            ? '单次咨询每回合用户响应时长'
            : '单次咨询用户响应时长'
        "
        prop="max_duration_hour"
        :rules="[
          {
            required: true,
            message: '单次咨询每回合用户响应时长',
            trigger: 'change'
          }
        ]"
      >
        <div class="form-item">
          <div>
            <el-input-number
              @change="onHourChange($event, 'duration')"
              size="small"
              :disabled="readOnly"
              v-model="value.max_duration_hour"
              :step="1"
              :min="0"
            ></el-input-number>
            <span>&nbsp;小时&nbsp;&nbsp;&nbsp;&nbsp;</span>
            <el-input-number
              size="small"
              v-model="value.max_duration_minute"
              :step="10"
              :min="getDelayMinuteFrom().duration"
              :max="59"
            ></el-input-number>
            <span>&nbsp;分钟</span>
          </div>
          <div class="red-tips margin-left-20">
            {{ serviceTips.ashHmMaxDuration }}
          </div>
        </div>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { serviceTips } from '@/utils/newService/serviceTips'
import moment from 'moment'

export default {
  name: 'HmForm',
  props: {
    value: {
      type: Object,
      default: () => {}
    },
    formType: {
      type: String,
      default: ''
    },
    readOnly: {
      type: Boolean,
      default: false
    },
    formRole: {
      type: String,
      default: '健康管理师'
    }
  },
  data() {
    return {
      serviceTips
    }
  },
  methods: {
    onHourChange(val, type) {
      if (type === 'first') {
        if (val === 0) {
          this.value.first_delay_minute = 10
        }
      } else if (type === 'common') {
        if (val === 0) {
          this.value.common_delay_minute = 10
        }
      } else if (type === 'duration') {
        if (val === 0) {
          this.value.max_duration_minute = 10
        }
      }
    },
    getDelayMinuteFrom() {
      let firstMinuteFrom = 15
      let commonMinuteFrom = 15
      let maxDurationMinuteFrom = 15
      if (this.value.first_delay_hour > 0) {
        firstMinuteFrom = 0
      }
      if (this.value.common_delay_hour > 0) {
        commonMinuteFrom = 0
      }
      if (this.value.max_duration_hour > 0) {
        maxDurationMinuteFrom = 0
      }
      return {
        first: firstMinuteFrom,
        common: commonMinuteFrom,
        duration: maxDurationMinuteFrom
      }
    },
    formatDuration(hour, minute) {
      const ISOString = `PT${hour}H${minute}M`
      return moment.duration(ISOString).asMinutes()
    }
  }
}
</script>

<style scoped lang="scss">
.form-item {
  display: flex;
}
.red-tips {
  font-size: 12px;
  color: #f8747f;
}
</style>
