import moment from 'moment'
function formatISODuration(hour, minute) {
  return `PT${hour}H${minute}M`
}
function formatISOMonth(month) {
  return `P${month}M`
}
function formatISOTimeFormNight(time) {
  const start = moment(time).startOf('days')
  const theTime = moment(time)
  const d = moment.duration(theTime.diff(start))
  return d.toISOString()
}
function parseDuration(time) {
  return Math.round(moment.duration(time).asMonths())
}

function getDelayHour(time) {
  return moment.duration(time).hours()
}
function getDelayMinutes(time) {
  return moment.duration(time).minutes()
}
function formatServiceTime(start, end) {
  const newStart = new Date(moment().startOf('days') + moment.duration(start))
  const newEnd = new Date(moment().startOf('days') + moment.duration(end))
  return [newStart, newEnd]
}
export function getParams(productForm) {
  const params = {
    ask_doctor: {},
    ask_dietitian: {},
    ask_hm: {},
    ask_common: {}
  }
  params.title = productForm.title
  params.sub_title = productForm.sub_title
  params.department = productForm.department
  params.market_price = productForm.market_price * 100
  params.price = productForm.price * 100
  params.base_price = productForm.base_price * 100
  params.duration = formatISOMonth(productForm.duration)
  params.corps = productForm.corps
  // 产科套餐无营养师团队
  if (params.department === 'ERKE') {
    params.dietitian_corps = productForm.dietitian_corps
  }
  params.ask_mode = productForm.ask_mode
  params.description = productForm.description
  params.advantage = productForm.advantage
  params.enabled_services = productForm.enabled_services
  params.ask_hm.service_days = productForm.serviceDays
  params.ask_hm.service_days_start = formatISOTimeFormNight(
    productForm.serviceTime[0]
  )
  params.ask_hm.service_days_end = formatISOTimeFormNight(
    productForm.serviceTime[1]
  )
  // 占用服务资源量
  params.ask_hm.resource = productForm.hmResource * 100
  if (params.department === 'ERKE') {
    params.ask_doctor.resource = productForm.doctorResource * 100
    params.ask_dietitian.resource = productForm.dietitianResource * 100
  }

  // 回合制和即时通讯ask_hm都需要传的参数
  params.ask_hm.first_delay = formatISODuration(
    productForm.ask_hm.first_delay_hour,
    productForm.ask_hm.first_delay_minute
  )
  params.ask_hm.common_delay = formatISODuration(
    productForm.ask_hm.common_delay_hour,
    productForm.ask_hm.common_delay_minute
  )
  // 新需求，即时通讯也需要上传用户响应时间
  params.ask_hm.user_delay = formatISODuration(
    productForm.ask_hm.max_duration_hour,
    productForm.ask_hm.max_duration_minute
  )
  // 回合制ask_hm需要传的参数
  if (productForm.ask_mode === 'ROUND') {
    params.ask_hm.counts = productForm.ask_hm.counts
    params.ask_hm.counts_per_month = productForm.ask_hm.counts_per_month
    params.ask_hm.round_counts = productForm.ask_hm.round_counts
    params.ask_hm.appendable = productForm.ask_hm.appendable
  }
  if (params.department === 'ERKE') {
    params.ask_doctor.enable = productForm.ask_doctor.enable
    params.ask_doctor.enable_voice = productForm.ask_doctor.enable_voice
    params.ask_dietitian.enable = productForm.ask_dietitian.enable
    params.ask_common.enable = false
  } else if (params.department === 'CHANKE') {
    params.ask_common.enable = productForm.ask_common.enable
    params.ask_common.enable_voice = productForm.ask_common.enable_voice
    params.ask_dietitian.enable = false
  }

  if (params.department === 'ERKE' && productForm.ask_doctor.enable) {
    // todo 允许医生图文转诊需要传的参数
    params.ask_doctor.counts = productForm.ask_doctor.counts
    params.ask_doctor.counts_per_month = productForm.ask_doctor.counts_per_month
    params.ask_doctor.first_delay = formatISODuration(
      productForm.ask_doctor.first_delay_hour,
      productForm.ask_doctor.first_delay_minute
    )
    params.ask_doctor.common_delay = formatISODuration(
      productForm.ask_doctor.common_delay_hour,
      productForm.ask_doctor.common_delay_minute
    )
    // 新需求，即时通讯也需要上传用户响应时间
    params.ask_doctor.user_delay = formatISODuration(
      productForm.ask_doctor.max_duration_hour,
      productForm.ask_doctor.max_duration_minute
    )
    // todo 回合制需要额外传的参数
    if (productForm.ask_mode === 'ROUND') {
      params.ask_doctor.round_counts = productForm.ask_doctor.round_counts
    }
  }
  if (params.department === 'ERKE' && productForm.ask_doctor.enable_voice) {
    params.ask_doctor.voice_counts = productForm.ask_doctor.voice_counts
    params.ask_doctor.voice_counts_per_month =
      productForm.ask_doctor.voice_counts_per_month
  }
  if (params.department === 'ERKE' && productForm.ask_dietitian.enable) {
    // 允许营养师图文转诊需要传的参数
    params.ask_dietitian.counts = productForm.ask_dietitian.counts
    params.ask_dietitian.counts_per_month =
      productForm.ask_dietitian.counts_per_month
    params.ask_dietitian.first_delay = formatISODuration(
      productForm.ask_dietitian.first_delay_hour,
      productForm.ask_dietitian.first_delay_minute
    )
    params.ask_dietitian.common_delay = formatISODuration(
      productForm.ask_dietitian.common_delay_hour,
      productForm.ask_dietitian.common_delay_minute
    )
    // 新需求，即时通讯也需要上传用户响应时间
    params.ask_dietitian.user_delay = formatISODuration(
      productForm.ask_dietitian.max_duration_hour,
      productForm.ask_dietitian.max_duration_minute
    )
    params.ask_dietitian.service_days = productForm.ask_dietitian.service_days
    params.ask_dietitian.service_days_start = formatISOTimeFormNight(
      productForm.ask_dietitian.serviceTime[0]
    )
    params.ask_dietitian.service_days_end = formatISOTimeFormNight(
      productForm.ask_dietitian.serviceTime[1]
    )
    // 回合制需要额外传的参数
    if (productForm.ask_mode === 'ROUND') {
      params.ask_dietitian.round_counts = productForm.ask_dietitian.round_counts
    }
  }
  // 产科家庭医生的参数
  if (params.department === 'CHANKE' && productForm.ask_common.enable) {
    // todo 允许医生图文转诊需要传的参数
    params.ask_common.counts = productForm.ask_common.counts
    params.ask_common.counts_per_month = productForm.ask_common.counts_per_month
    params.ask_common.first_delay = formatISODuration(
      productForm.ask_common.first_delay_hour,
      productForm.ask_common.first_delay_minute
    )
    params.ask_common.common_delay = formatISODuration(
      productForm.ask_common.common_delay_hour,
      productForm.ask_common.common_delay_minute
    )
    // 新需求，即时通讯也需要上传用户响应时间
    params.ask_common.user_delay = formatISODuration(
      productForm.ask_common.max_duration_hour,
      productForm.ask_common.max_duration_minute
    )
    // todo 回合制需要额外传的参数
    if (productForm.ask_mode === 'ROUND') {
      params.ask_common.round_counts = productForm.ask_common.round_counts
    }
    // 产科家庭医生，转诊其他医生，需要设定服务时间
    params.ask_common.service_days = productForm.ask_common.service_days
    params.ask_common.service_days_start = formatISOTimeFormNight(
      productForm.ask_common.serviceTime[0]
    )
    params.ask_common.service_days_end = formatISOTimeFormNight(
      productForm.ask_common.serviceTime[1]
    )
  }
  if (params.department === 'CHANKE' && productForm.ask_common.enable_voice) {
    params.ask_common.voice_counts = productForm.ask_common.voice_counts
    params.ask_common.voice_counts_per_month =
      productForm.ask_common.voice_counts_per_month
  }
  // 需求修改，产科家庭医生绑定的参数是ask_doctor,将原来的ask_hm字段赋值给ask_doctor
  if (params.department === 'CHANKE') {
    params.ask_doctor = { ...params.ask_hm, enable: true, enable_voice: false }
    params.ask_hm = { enable: false }
  }

  return params
}

export const setParams = function(resData) {
  // 返回数据中不需要处理即可直接赋值的参数
  const {
    title,
    sub_title,
    department,
    corps,
    dietitian_corps,
    ask_mode,
    description,
    advantage,
    enabled_services
  } = resData
  let productForm = {
    title,
    sub_title,
    department,
    corps,
    dietitian_corps,
    ask_mode,
    description,
    advantage,
    enabled_services
  }
  // 需要简单处理赋值的参数
  productForm.market_price = resData.market_price / 100
  productForm.price = resData.price / 100
  productForm.base_price = resData.base_price / 100
  productForm.duration = parseDuration(resData.duration)
  // 需求变更，产科需要做ask_doctor到ask_hm的转化
  if (productForm.department === 'CHANKE') {
    resData.ask_hm = resData.ask_doctor
  }
  // 需要从ask_hm中赋值的参数
  productForm.serviceDays = resData.ask_hm.service_days
  // 将ISO字符串转化为时间选择器时间
  productForm.serviceTime = formatServiceTime(
    resData.ask_hm.service_days_start,
    resData.ask_hm.service_days_end
  )
  productForm.hmResource = resData.ask_hm.resource / 100

  // 需要从ask_doctor中赋值的参数
  productForm.doctorResource = resData.ask_doctor.resource / 100

  // 需要从ask_dietitian中赋值的参数
  productForm.dietitianResource = resData.ask_dietitian.resource / 100

  const ask_hm = {
    counts: resData.ask_hm.counts,
    counts_per_month: resData.ask_hm.counts_per_month,
    round_counts: resData.ask_hm.round_counts,
    appendable: resData.ask_hm.appendable,
    first_delay_hour: getDelayHour(resData.ask_hm.first_delay),
    first_delay_minute: getDelayMinutes(resData.ask_hm.first_delay),
    common_delay_hour: getDelayHour(resData.ask_hm.common_delay),
    common_delay_minute: getDelayMinutes(resData.ask_hm.common_delay),
    max_duration_hour: getDelayHour(resData.ask_hm.user_delay),
    max_duration_minute: getDelayMinutes(resData.ask_hm.user_delay)
  }
  productForm.ask_hm = ask_hm
  const ask_doctor = {
    enable: resData.ask_doctor.enable,
    enable_voice: resData.ask_doctor.enable_voice,
    counts: resData.ask_doctor.counts,
    counts_per_month: resData.ask_doctor.counts_per_month,
    round_counts: resData.ask_doctor.round_counts,
    first_delay_hour: getDelayHour(resData.ask_doctor.first_delay),
    first_delay_minute: getDelayMinutes(resData.ask_doctor.first_delay),
    common_delay_hour: getDelayHour(resData.ask_doctor.common_delay),
    common_delay_minute: getDelayMinutes(resData.ask_doctor.common_delay),
    max_duration_hour: getDelayHour(resData.ask_doctor.user_delay),
    max_duration_minute: getDelayMinutes(resData.ask_doctor.user_delay),
    voice_counts: resData.ask_doctor.voice_counts,
    voice_counts_per_month: resData.ask_doctor.voice_counts_per_month
  }
  productForm.ask_doctor = ask_doctor
  const ask_dietitian = {
    enable: resData.ask_dietitian.enable,
    counts: resData.ask_dietitian.counts,
    counts_per_month: resData.ask_dietitian.counts_per_month,
    round_counts: resData.ask_dietitian.round_counts,
    first_delay_hour: getDelayHour(resData.ask_dietitian.first_delay),
    first_delay_minute: getDelayMinutes(resData.ask_dietitian.first_delay),
    common_delay_hour: getDelayHour(resData.ask_dietitian.common_delay),
    common_delay_minute: getDelayMinutes(resData.ask_dietitian.common_delay),
    max_duration_hour: getDelayHour(resData.ask_dietitian.user_delay),
    max_duration_minute: getDelayMinutes(resData.ask_dietitian.user_delay),
    service_days: resData.ask_dietitian.service_days,
    serviceTime: formatServiceTime(
      resData.ask_dietitian.service_days_start,
      resData.ask_dietitian.service_days_end
    )
  }
  productForm.ask_dietitian = ask_dietitian
  // 产科医生，其他医生服务的数据回显
  const ask_common = {
    enable: resData.ask_common.enable,
    enable_voice: resData.ask_common.enable_voice,
    counts: resData.ask_common.counts,
    counts_per_month: resData.ask_common.counts_per_month,
    round_counts: resData.ask_common.round_counts,
    first_delay_hour: getDelayHour(resData.ask_common.first_delay),
    first_delay_minute: getDelayMinutes(resData.ask_common.first_delay),
    common_delay_hour: getDelayHour(resData.ask_common.common_delay),
    common_delay_minute: getDelayMinutes(resData.ask_common.common_delay),
    max_duration_hour: getDelayHour(resData.ask_common.user_delay),
    max_duration_minute: getDelayMinutes(resData.ask_common.user_delay),
    service_days: resData.ask_common.service_days,
    serviceTime: formatServiceTime(
      resData.ask_common.service_days_start,
      resData.ask_common.service_days_end
    ),
    voice_counts: resData.ask_common.voice_counts,
    voice_counts_per_month: resData.ask_common.voice_counts_per_month
  }
  productForm.ask_common = ask_common

  return productForm
}
